const Precio = (props) => {
    console.log(props);
    const {rate_float, code} = props.precio;
    const rate = rate_float.toFixed(2); //para que redondee el precio a 2 cifras decimales porque el float puede tener m√°s de 2

    return ( 
        <div className="card text-white bg-success mb-3">
            <div className="card-header">Precio del Bitcoin</div>
            <div className="card-body">
                <h4 className="card-title">Precio actual: $ {rate} {code}</h4>
            </div>
        </div>
     );
}
 
export default Precio;